import React from 'react';
import SiteHead from '../../components/SiteHead';
import meta from './meta.json';
import links from './links.json';
import './index.css';

class RedirectPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      to: this.getRedirectLink(),
      remaining: 10
    }
  }

  getQueryString() {
    let queryKeys = {};
    let queryString = window.location.search.substring(1);
    let queryKeyValues = queryString.split('&');

    for(let i in queryKeyValues) {
      let key = queryKeyValues[i].split('=');
      if (key.length > 1) {
        queryKeys[key[0]] = key[1];
      }
    }

    return queryKeys;
  }

  getRedirectLink() {
    let qs = this.getQueryString();

    if (qs.to && Object.keys(links).includes(qs.to)) {
      return links[qs.to];
    }
    else {
      return 'https://bastionbot.org';
    }
  }

  redirect() {
    window.location = this.state.to;
  }

  cancel() {
    clearTimeout(this.state.timeout);

    this.setState({
      remaining: -1
    });
  }

  componentDidMount() {
    let timeout = setTimeout(() => {
      this.redirect();
    }, 10000);

    setInterval(() => {
      this.setState({
        remaining: this.state.remaining > 0 ? this.state.remaining - 1 : this.state.remaining
      });
    }, 1000);

    this.setState({
      timeout
    });
  }

  render() {
    return (
      <div id='redirect'>
        <SiteHead
          title={ meta.title }
        />

        <div className='container'>
          <div className='redirectCard'>
            <h2>Redirecting...</h2>
            <p>You are being redirected to...</p>
            <pre><a href={ this.state.to }><code>{
              this.state.to
            }</code></a></pre>
            <p>...in { this.state.remaining } seconds.</p>
            <button onClick={() => this.cancel()}>Cancel</button>
          </div>
        </div>
      </div>
    );
  }
}

export default RedirectPage;



